import React, { Component } from 'react'
import * as THREE from 'three'

class Scene extends Component {

  constructor(props) {
    super(props)

    this.scene = new THREE.Scene()
    this.camera = new THREE.PerspectiveCamera( 75, window.innerWidth/window.innerHeight, 0.1, 1000 )

    this.renderer = new THREE.WebGLRenderer()
    this.renderer.setSize( window.innerWidth, window.innerHeight )
    document.body.appendChild( this.renderer.domElement )

    var sphere_geometry = new THREE.SphereGeometry()
    var sphere_material = new THREE.MeshBasicMaterial( { color: 0x00ff00 } )
  	this.cube = new THREE.Mesh( sphere_geometry, sphere_material )
  	this.scene.add( this.cube )

    var ellipse_curve = new THREE.EllipseCurve( 1, 2, 1, 2 )
    var ellipse_points = ellipse_curve.getPoints( 50 )
    var ellipse_geometry = new THREE.BufferGeometry().setFromPoints( ellipse_points )
    var ellipse_material = new THREE.LineBasicMaterial( { color : 0xff0000 } )
    var ellipse = new THREE.Line( ellipse_geometry, ellipse_material )
    this.scene.add( ellipse )

  	this.camera.position.z = 5

    this.animate = this.animate.bind(this)

    this.animate()
  }

  animate() {
		requestAnimationFrame( this.animate )

		this.cube.rotation.x += 0.1
		this.cube.rotation.y += 0.1

		this.renderer.render(this.scene, this.camera)
  }

  render() {
    return(
      <div></div>
    )
  }

}

export default Scene



